import { Link, useLocation } from "react-router-dom";

export const MoviesList = ({ movies }) => {
    
    const location = useLocation();
    
    return (
        <div>
            <ul>
                {movies && movies.map(movie =>
                    <li key={movie.id}>
                        <Link to={`/movies/${movie.id}`} state={{ from: location }}>{movie.title}</Link>
                    </li>
                )}
            </ul>
        </div>
    )
}